import React from "react";

const OrderList = () => {
  const orders = [
    { id: 1, partCode: "SN.czsci", partName: "Nazwa czci " },
    { id: 2, partCode: "SN.czsci", partName: "Nazwa czci " },
    { id: 3, partCode: "SN.czsci", partName: "Nazwa czci " },
    { id: 4, partCode: "SN.czsci", partName: "Nazwa czci " },
  ];

  return (
    <div className="order-list">
      <h2>Lista Zamwie</h2>
      <ul className="list">
        {orders.map((order) => (
          <li key={order.id} className="list-item order-item-transform">
            Nazwa Urzdzenia | {order.partCode} | {order.partName}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default OrderList;
